<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="130px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">{{form.work_status_name}}({{form.stage_name}})</span></h2>
                    <p class="fs-14">当前处理人：{{form.user_name}}</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车牌号">
                                {{form.base_info.plate_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="车主姓名">
                                {{form.base_info.band_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="vin号">
                                {{form.base_info.vin_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="移动电话">
                                {{form.base_info.mobile}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车辆品牌">
                                {{form.base_info.car_brand}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="挂靠公司">
                                {{form.base_info.bus_mech_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公告型号">
                                {{form.base_info.foreign_model}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="马力">
                                {{form.base_info.horse_power}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="驱动形式">
                                {{form.base_info.drive_form}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="颜色">
                                {{form.base_info.color}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="选择办理人">
                                <el-radio-group v-model="form.tran_type" disabled>
                                    <el-radio label="2">专业人士</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="办理人姓名">
                                <el-input v-model.trim="form.tran_name" placeholder="请输入" :disabled="isDisabledTransactor()"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="办理人移动电话">
                                <el-input v-model="form.telphone" placeholder="请输入" :disabled="isDisabledTransactor()"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　违章信息" name="2">
                    <div class="toolbar ta-r mb10" v-if="isShowToSubmit()">
                        <el-button type="primary" size="mini" plain @click="handleAddAccident">添加事故</el-button>
                    </div>
                    <el-table :data="form.acc_list" border class="table" ref="multipleTable">
                        <el-table-column prop="name" label="违章名称" width="150"></el-table-column>
                        <el-table-column prop="name" label="违章地点" width="120"></el-table-column>
                        <el-table-column prop="date" label="事故时间"></el-table-column>
                        <el-table-column prop="deduct_score" label="扣分"></el-table-column>
                        <el-table-column prop="fine" label="罚款"></el-table-column>
                        <el-table-column prop="handle_img" label="处理凭证" v-if="form.node_id>=226"></el-table-column>
                        <el-table-column label="操作" width="180" align="center" fixed="right">
                            <template slot-scope="{row, $index}">
                                <el-button type="text" icon="el-icon-view" @click="handleUploadData(row, $index)" v-if="form.node_id==226">上传凭证</el-button>
                                <el-button type="text" icon="el-icon-delete" @click="handleDelete(row, $index)" v-if="isShowToSubmit()">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    
                </el-collapse-item>

                <el-collapse-item title="　收款信息" name="3">

                    <el-form-item label="收款信息选择">
                        <el-radio-group v-model="form.coll_info.pay_type" :disabled="isDisabledToSubmit()">
                            <el-radio label="1">立即支付</el-radio>
                            <el-radio label="2">延迟付款</el-radio>
                            <el-radio label="3">已支付</el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="费用金额" :disabled="isDisabledToSubmit()" v-if="form.coll_info.pay_type == 2">
                        <el-input v-model="form.coll_info.money"></el-input>
                    </el-form-item>

                    <el-row v-if="form.coll_info.pay_type == 1">
                        
                        <el-form-item label="车主支付方式">
                            <el-radio-group v-model="form.coll_info.pay_method" :disabled="isDisabledToSubmit()">
                                <el-radio label="1">现付</el-radio>
                                <el-radio label="2">转账</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="本次费用">
                            <el-input v-model="form.coll_info.this_cost" @change="handelOutstanding" :disabled="isDisabledToSubmit()"></el-input>
                        </el-form-item>

                        <el-form-item label="未结清费用">
                            <el-checkbox-group v-model="form.no_pay_money_total" @change="handelOutstanding" :disabled="isDisabledToSubmit()">
                                <el-checkbox
                                    v-for="item in form.no_pay_money"
                                    :key="item.id"
                                    :label="item.id">
                                    {{item.item_name}} ￥{{item.money}}
                                </el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                        <el-form-item label="费用凭证">
                            <el-upload
                                :disabled="isDisabledToSubmit()"
                                class="avatar-uploader"
                                :action="IMG_UPLOAD_URL"
                                :show-file-list="false"
                                :on-success="handleUploadSuccess_cost_img"
                                :before-upload="handleBeforeUpload">
                                <img v-if="form.coll_info.cost_img" :src="form.coll_info.cost_img" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>

                        <el-form-item label="支付总费用">
                            {{form.coll_info.together_cost}}
                        </el-form-item>
                        
                    </el-row>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.coll_info.remark" :disabled="isDisabledToSubmit()"></el-input>
                    </el-form-item>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowFirstAuditor()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　付款信息" name="4" v-if="form.node_id>=218">

                    <el-form-item label="付款金额">
                        <el-input v-model="form.pay_info.pay_money"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行">
                        <el-input v-model="form.pay_info.pay_bank"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行账户">
                        <el-input v-model="form.pay_info.pay_bank_account"></el-input>
                    </el-form-item>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.pay_info.pay_remark"></el-input>
                        <p>注：请填写支付专业人士办理业务的费用</p>
                    </el-form-item>

                    <el-form-item label="出纳打款凭证"v-if="isShowVoucher()">
                        <el-upload
                            :disabled="isDisabledVoucher()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_pay_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.pay_info.pay_img" :src="form.pay_info.pay_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    
                    <el-form-item label="备注" v-if="isShowVoucher()">
                        <el-input type="textarea" v-model="form.pay_info.pay_img_remark" :disabled="isDisabledVoucher()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="5" v-if="form.node_id!=210 && form.node_id!=0">
                    
                    <el-collapse v-model="collapseAuditActive">

                        <el-card class="box-card mb15" v-for="(item,index) in form.audit_info.pay" :key="index">
                            <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                            <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                            <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                            <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                            <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                        </el-card>

                        <!-- <el-collapse-item title="　收款审核" name="1">
                            <el-card class="box-card mb15">
                                <div><span class="display-ib ta-r w70">订单流程：</span>定金审核 - 财务</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>通过</div>
                                <div><span class="display-ib ta-r w70">备注：</span>撒打算</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span> asdas</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>2018-10-23 00:49:38</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　付款审核" name="2">
                            <el-card class="box-card mb15">
                                <div><span class="display-ib ta-r w70">订单流程：</span>定金审核 - 财务</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>通过</div>
                                <div><span class="display-ib ta-r w70">备注：</span>撒打算</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span> asdas</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>2018-10-23 00:49:38</div>
                            </el-card>
                        </el-collapse-item> -->

                        <template v-if="isShowOpinion()">
                            <el-form-item label="审核意见" class="mt15">
                                <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                    <el-option label="通过" value="1"></el-option>
                                    <el-option label="驳回" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.remark"></el-input>
                            </el-form-item>
                            <el-form-item label="选择审核人" class="mt15" v-if="form.opinion==1">
                                <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </template>

                        <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                            <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>

                    </el-collapse>
                </el-collapse-item>
            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提 交</el-button>
                <el-button type="primary" @click="handleSubmit(2)" v-if="isShowSave()">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>

        <!-- 违章信息 -->
        <el-dialog title="违章信息" :visible.sync="dialogAddAccident" width="50%">
            <el-form ref="filtersAddAccident" :model="filtersAddAccident" label-width="80px">
                <el-form-item label="违章名称">
                    <el-input v-model="filtersAddAccident.name"></el-input>
                </el-form-item>
                <el-form-item label="违章地点">
                    <el-input v-model="filtersAddAccident.address"></el-input>
                </el-form-item>
                <el-form-item label="违章时间">
                    <el-date-picker
                        v-model="filtersAddAccident.date"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="扣分">
                    <el-input v-model="filtersAddAccident.deduct_score"></el-input>
                </el-form-item>
                <el-form-item label="罚款">
                    <el-input v-model="filtersAddAccident.fine"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogAddAccident = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitAccident">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 上传资料 -->
        <el-dialog title="上传资料" :visible.sync="dialogUploadData" width="50%">
            <el-form ref="filtersUploadData" :model="filtersUploadData">
                <el-form-item label=" ">
                    请选择需要上传的证件照片：
                </el-form-item>
                <el-form-item label="处理凭证">
                    <div slot="tip" class="el-upload__tip">请选择需要上传的证件照片：</div>
                    <el-upload
                        class="avatar-uploader"
                        :action="IMG_UPLOAD_URL"
                        :show-file-list="false"
                        :on-success="handleUploadSuccess_handle_img"
                        :before-upload="handleBeforeUpload">
                        <img v-if="filtersUploadData.handle_img" :src="filtersUploadData.handle_img" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label=" ">
                    只能上传jpg/png文件，且不超过500kb
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogUploadData = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitUploadData">确 定</el-button>
            </span>
        </el-dialog>
        
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                pageType: '',
                loading: true,
                activeAccList: '',
                dialogUploadData: false,
                dialogAddAccident: false,
                filtersUploadData: {
                    handle_img: ''
                },
                filtersAddAccident: {
                    name:'',
                    address:'',
                    date:'',
                    deduct_score:'',
                    fine:'',
                },
                optionsCompany:[],
                optionsExecUser:[],

                form: 

{
    three_sides_img: [],
    opinion: '1',
    cp_id:'',
    after_user_id:'',

    tran_type: "2",
    tran_name: "",
    telphone: "",

    remark: '',
    node_id: "",
    create_time: "",
    update_time: "",
    no_pay_money: [
        {
            id: "29",
            item_id: "119",
            item_name: "运营证年检",
            money: "1"
        },
        {
            id: "30",
            item_id: "120",
            item_name: "运营证年检",
            money: "1231"
        },
        {
            id: "31",
            item_id: "121",
            item_name: "运营证年检",
            money: "1231"
        },
        {
            id: "32",
            item_id: "122",
            item_name: "运营证年检",
            money: "1231"
        }
    ],
    no_pay_money_total: [],
    base_info: {
        id: "",
        vin_number: "",
        car_brand: "",
        car_series: "",
        car_class: "",
        foreign_model: "",
        fuel_type: "",
        horse_power: "",
        drive_form: "",
        color: "",
        band_id: "",
        band_name: "",
        bus_mech_name: "",
        plate_number: "",
        drive_license_number: "",
        drive_license_register: "",
        drive_license_expire: "",
        op_license_number: "",
        op_license_register: "",
        op_license_expire: "",
        id_card: "",
        mobile: ""
    },
    acc_list: [
        {
            id: "",
            type: "",
            item_id: "",
            name: "",
            address: "",
            date: "",
            remark: "",
            event: "",
            deduct_score: "",
            fine: "",
            handle_img: "",
            status: "",
            update_time: "",
            create_time: "",
            operation_uniqid:""
        },
    ],
    coll_info: {
        coll_id:'',
        money: '',
        pay_type: '1',
        pay_method: '1',
        this_cost: '',
        non_payment: '',
        no_pay_id: '',
        together_cost: '',
        cost_img: '',
        remark: ''
    },
    pay_info: {
        pay_id:'',
        pay_money: '',
        pay_bank: '',
        pay_bank_account: '',
        pay_remark: '',
        pay_img: '',
        pay_img_remark: ''
    },
}

                
            }
        },
        computed:{
            grossAmount(){
                return this.form.coll_info.this_cost
            }
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            isDisabledTransactor(){
                return this.form.node_id!=210 && this.form.node_id!=0
            },
            isDisabledToSubmit(){
                return this.form.node_id!=210 && this.form.node_id!=217 && this.form.node_id!=0
            },
            isDisabledPayment(){
                return this.form.node_id!=218 && this.form.node_id!=225
            },
            isDisabledVoucher(){
                return this.form.node_id > 224
            },
            isShowToSubmit(){
                return this.form.node_id==210 || this.form.node_id==217 || this.form.node_id==0
            },
            isShowLastAudit(){
                return this.form.work_status==1 || this.form.work_status==3
            },
            isShowSave(){
                if(this.pageType != 'view'){
                    return this.form.work_status!=2
                }
            },
            isShowVoucher(){
                return this.form.node_id >= 224
            },
            isShowOpinion(){
                return this.form.work_status==2 && this.form.node_id!=226
            },
            isShowFirstAuditor(){
                return this.form.node_id==201 || this.form.node_id==217  || this.form.node_id==0
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                this.loading=true
                API.apiViolationView({id:_query.id,operation_uniqid:_query.uniqid}).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        Object.assign(this.form, res.data);
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let type='',
                    get_type=''
                if(this.form.coll_info.pay_type == 1){
                    this.form.coll_info.pay_method==1?type=1:type=2
                }else{
                    type=3
                }
                if(this.form.stage_id==7 || this.form.stage_id==0){
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=1
                    }else{
                        get_type=2
                    }
                }else{
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=3
                    }else{
                        get_type=2
                    }
                }
                let params = {
                    cp_id:this.form.cp_id,
                    type:type,
                    get_type:get_type,
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiViolationAuditUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleSubmit(submitType) {
                if(this.form.node_id == 210 || this.form.node_id == 0){
                    let params = {
                        ...this.form.coll_info,
                        id: this.form.id,
                        bus_id: this.form.bus_id,
                        operation_uniqid:this.form.operation_uniqid,
                        acc_list: this.form.acc_list,
                        tran_type: this.form.tran_type,
                        tran_name: this.form.tran_name,
                        telphone: this.form.telphone,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                    }
                    API.apiViolationAddFistInfo(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 217){
                    let params = {
                        ...this.form.coll_info,
                        bus_id: this.form.bus_id,
                        operation_uniqid:this.form.operation_uniqid,
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                    }
                    API.apiViolationUpdateCollInfo(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 218){
                    let params = {
                        id: this.form.id,
                        ...this.form.pay_info,
                        operation_uniqid:this.form.operation_uniqid,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        
                    }
                    API.apiViolationHandleWait(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 225){
                    let params = {
                        ...this.form.pay_info,
                        bus_id: this.form.bus_id,
                        operation_uniqid:this.form.operation_uniqid,
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                    }
                    API.apiViolationUpdatePayInfo(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 226){
                    let params = {
                        acc_list: this.form.acc_list,
                        bus_id: this.form.bus_id,
                        operation_uniqid:this.form.operation_uniqid,
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        exam_type: this.form.opinion,
                        perfect_info: 1
                    }
                    API.apiViolationAddOwnLast(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let payParams={
                        pay_img: this.form.pay_info.pay_img,
                        pay_id: this.form.pay_info.pay_id,
                        pay_img_remark: this.form.pay_info.pay_img_remark,
                    }
                    let params = {
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        operation_uniqid:this.form.operation_uniqid,
                        remark: this.form.remark,
                        exam_type: this.form.opinion,
                        reject_type: this.form.stage_id==7?'1':'2',
                    }
                    if(this.form.node_id==224){
                        Object.assign(params, payParams)
                    }
                    API.apiViolationCollPayAudit(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
                
            },
            handelOutstanding(){
                let num = 0
                if(this.form.no_pay_money_total.length<=0){
                    this.form.coll_info.together_cost = this.form.coll_info.this_cost
                }else{
                    this.form.no_pay_money.forEach(item2=>{
                        this.form.no_pay_money_total.forEach(item=>{
                            if(item2.id==item){
                                num+=Number(item2.money)
                            }
                        })
                    })
                    this.form.coll_info.together_cost = num+Number(this.form.coll_info.this_cost)
                }
            },
            handleUploadSuccess_cost_img(res, file, fileList){
                this.form.coll_info.cost_img = res.data.url
            },

            handleUploadSuccess_three_sides_img(res, file, fileList){
                this.form.three_sides_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_pay_img(res, file, fileList){
                this.form.pay_info.pay_img = res.data.url
            },
            handleUploadSuccess_handle_img(res, file, fileList){
                this.filtersUploadData.handle_img = res.data.url
            },
            handleUploadSuccess(res, file, fileList){
                this.drive_license_img = res.data.url
            },
            handleBeforeUpload(){

            },
            handleAddAccident() {
                this.dialogAddAccident = true;
            },
            dialogSubmitAccident(){
                this.dialogAddAccident = false
                this.form.acc_list.push(this.filtersAddAccident)
            },
            handleDelete(row, index){
                this.form.acc_list.splice(index, 1);
            },
            handleUploadData(row, index){
                this.dialogUploadData = true
                this.activeAccList = index
                Object.assign(this.filtersUploadData,row)
            },
            dialogSubmitUploadData(){
                this.dialogUploadData = false
                this.form.acc_list[this.activeAccList].handle_img = this.filtersUploadData.handle_img
            }
        }
    }
</script>

